import Vue from 'vue'
import Router from 'vue-router'
import SpoonKeitodama from '../components/SpoonKeitodama'
import Home from '../components/Home'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/SpoonKeitodama',
      name: 'SpoonKeitodama',
      component: SpoonKeitodama
    }
  ]
})



